//
// Node links
// =================================
.field--name-node-link .field__items a,  .more-link a {
	@include fontello-icon('\e800','after') {
		font-size: 1.0em;
		margin-left: 0.3em;
		margin-right: 0;
		position: relative;
		color: $orange;
		cursor: pointer;
	}

}

.field--name-node-link .field__items, .more-link {

	a {
		color: $dark-cyan;
		text-transform: uppercase;
		font-weight: 600;
		font-size: $fz-read-more;
	}

}


//
// Main Menu
// =================================
#block-menu-block-2 .menu-block-2 li a {
	font-size: $fz-menu;
	line-height: $line-height-menu;
	color: $color-menu-item;
	font-weight: $fw-bold;
}

//
// Main Menu overrides for responsive menu
// =================================
.responsive-menus.responsified {
	
	span.toggler {
		background: $violet-dark;
		box-shadow: none;
		color: $white;
		border-radius: 0;
		box-shadow: none;
		float: right;
		padding: 8px 4px;
		@include fontello-icon('\e808') {
			font-size: 1.7em;
			cursor: pointer;
			color: $white;
			position: relative;
			margin-right: 10px;
		}

	}
	.responsive-menus-simple {
		top: 50px !important;
		background: #333;
		border-bottom-right-radius: 0;
		border-bottom-left-radius: 0;
		box-shadow: none;
		li a {
			background: $violet-dark;
			color: $white !important;
			//		border-bottom:1px solid ;
		}

	}

}



//
// Menus in Footer First
// =================================
.block--menu h2 {
	font-size: $fz-h2;
	color: $white;
}
.menu .leaf {
	list-style-image: none;
	list-style-type: none;
	font-size: $fz-menu-footer;
	line-height: $line-height-footer-menu;
}

#block-menu-menu-footer:before {
	display: block;
	content: 'Site by ';
	left: -42px;
	top: 1px;
	position: absolute;
	font-size: $fz-small;
	color: #333333;
}
;

//
// MENU BLOCK 2 <header Block>
// =================================
#block-menu-block-2 {
	position: absolute;
	top: 22px;
	width: 100%;
	.menu-name-main-menu {
		ul.menu {
			list-style-type: none;
			margin: 0;
			padding: 0;
			
			li {
				padding: 10px 5px;
				width: 120px;
				display: inline-block;
				text-align: center;
				a {
					text-decoration: none;
				}

			}
			li.active-trail {
				background: $violet-super-super-light;
			}

		}

	}

}

//
//BreadCrumb
// =================================
ul.breadcrumb {
	margin-top: 25px;
	margin-left: 10px;
	li {
		font-size: $fz-small;
		line-height: 25px;
		color: $violet-dark !important;
		font-weight: $fw-bold;
		display: inline-block;
		@include fontello-icon('/','after') {
			font-size: 0.8em;
			font-weight: $fw-bold;
			margin-left: 0.3em;
			margin-right: 0;
			position: relative;
			color: $grey-dark;
			cursor: pointer;
		}
		a {
			font-size: $fz-small;
			line-height: 25px;
			color: $violet-dark !important;
			font-weight: $fw-bold;
		}

	}
	li.last:after {
		content: '';
	}
	;
}

//TABS
.tabs {
	border-bottom: 6px solid $tabs-color;
	li.active {
		a {
			background: $tabs-color !important;
		}

	}
	li {
		a {
			color: $violet-dark !important;
			font-size: $fz-mid;
			text-transform: uppercase;
			font-weight: $fw-bold;
			background: -webkit-linear-gradient($violet-super-super-light, $tabs-color); /* For Safari 5.1 to 6.0 */
			background: -o-linear-gradient($violet-super-super-light, $tabs-color); /* For Opera 11.1 to 12.0 */
			background: -moz-linear-gradient($violet-super-super-light, $tabs-color); /* For Firefox 3.6 to 15 */
			background: linear-gradient($violet-super-super-light, $tabs-color); /* Standard syntax */
			padding:10px 30px;
		}
		a.active {
			color: $grey-dark !important;
		}

	}

}
